.video-react-big-play-button, .video-react-control-bar  {
    display: none;
}

video{
    margin-top: 1%;
    vertical-align: center;
    text-align: center;
    width: 60%;
    height: 60%;
    margin-left: 20%;
}

.menu-rosado > .focused-item {
    color: white !important ;
    font-weight: bold;
}


.focused-item{
    color: red !important ;
}

.item-focusable:focus{
    outline: none !important;
}

.App > div > div >  .focused-item{
    background-color: white !important ;
}

.link-mapa-buscador > .focused-item > svg{
        color: white !important ;
}

#divGrande > div > .focused-item{
    color: red !important ;
}

.menu-item {
  padding: 0 40px;
  margin: 5px 10px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  cursor: pointer;
  border: none;
}

.scroll-menu-arrow {
  /* padding: 20px; */
  cursor: pointer;
}

img{
  -webkit-filter: grayscale(100%);
          filter: grayscale(100%);
  opacity: 0.6;
}

.focused-item img{
  -webkit-filter: none;
          filter: none;
  opacity: 1;
}

.item .feria-text{
  opacity: 0.6;
}

.focused-item .item .feria-text{
  opacity: 0.6;
}

.btn-elegir-tv{
  background: rgb(151, 151, 151);
}

.btn-elegir-tv.focused-item{
  background: rgb(237, 33, 124) !important;
  color: #fff !important;
}

.focused-item.feria > .feria-text{
  color: #ed217c !important;
}


.lista{
  overflow-x: hidden;
  overflow-y: hidden;
  display: flex;
  white-space:nowrap;
}

.item-lista{
  box-shadow: 1px 1px 10px #999; 
  margin: 2px;
  max-height: 50px;
  cursor: pointer;
  display:inline-block;
  *display:inline;
  *zoom:1;
  vertical-align:top;

}

.wrapper{
  /* background:#EFEFEF;  */
  box-shadow: 1px 1px 10px #999; 
  margin: auto; 
  text-align: center; 
  position: relative;
  border-radius: 5px;
  margin-bottom: 20px !important;
  width: 800px;
  padding-top: 5px;
}

body{
  font-family: 'Roboto', sans-serif; 
}
.simple-keyboard {
    max-width: 600px;
    max-height: 500px;
    margin-left: 4%;
    margin-top: 5%;
    height: 100%;
}

input {
    width: 400px;
    height: 50px;
    padding: 20px;
    font-size: 20px;
    margin-left: 42%;
    margin-top:22%;
    margin-bottom: 5%;
    border: none;
    box-sizing: border-box;
    background-color: #ed217c;
    color: white;
}

::-webkit-input-placeholder {
    color: white;
  }

::-moz-placeholder {
    color: white;
  }

:-ms-input-placeholder {
    color: white;
  }

::-ms-input-placeholder {
    color: white;
  }

::placeholder {
    color: white;
  }

.simple-keyboard  > .hg-row > .focused-item{
    background-color: #ed217c !important ;
    color: white !important;
}

.resultado-container > a > .focused-item{
    /* background-color: #ed217c !important ; */
    color: #ed217c !important;
}


  
